@mixin watch {
  @media screen and (max-width: $breakpoint-sm) {
    @content;
  }
}

@mixin mobile {
  @media screen and (min-width: $breakpoint-sm) {
    @content;
  }
}

@mixin tablet {
  @media screen and (min-width: $breakpoint-md) {
    @content;
  }
}

@mixin desktop {
  @media screen and (min-width: $breakpoint-lg) {
    @content;
  }
}

.container {
  margin: 0 auto;
  padding: 0 1rem;

  @include mobile {
    width: 100mv;
  }

  @include desktop {
    width: $breakpoint-lg;
  }

}
